nd: white;\n  height: 100%;\n  width: 240px;\n  min-width: 240px;\n  overflow: hidden;\n  overflow-y: auto;\n}\n.users-list button, .conversations-list button {\n  margin-top: 2px;\n  margin-bottom: 2px;\n  padding-left: 2px;\n  padding-right: 2px;\n  width: 100%;\n  text-align: start;\n}\n.users-list button mat-icon, .conversations-list button mat-icon {\n  margin-left: 4px;\n  margin-right: 4px;\n  opacity: 0.9;\n}\n.users-list .title, .conversations-list .title {\n  border-bottom: 1px solid #3f51b5;\n  text-align: center;\n  width: 100%;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  height: 42px;\n  line-height: 42px;\n  vertical-align: middle;\n}\n.full-message-window {\n  margin-right: 0 !important;\n}\n.right-panel-show {\n  margin-right: 0 !important;\n}\n.right-panel-hide {\n  margin-right: -240px !important;\n}\n.chat-button {\n  margin-top: 18px;\n  margin-bottom: 18px;\n}\n.hidden {\n  display: none;\n}\n@media only screen and (max-device-width: 640px) {\n  /* Styles */\n  app-messages-window {\n    margin-right: 0;\n  }\n\n  .users-list {\n    margin-right: -240px;\n  }\n\n  .conversations-list {\n    margin-right: -240px;\n  }\n}\n/* break point for material toolbar (from 64px height to 56px) */\n@media only screen and (max-device-width: 599px) {\n  :host {\n    height: calc(100% - 152px);\n    /* 160px = top bar height 56 + bottom bar height 96 */\n    font-size: 85%;\n  }\n}\n/* Responsive text size */\n@media only screen and (max-device-width: 1280px) {\n  :host {\n    font-size: 95%;\n  }\n}\n@media only screen and (max-device-width: 1024px) {\n  :host {\n    font-size: 90%;\n  }\n}\n@media only screen and (max-device-width: 599px) {\n  :host {\n    font-size: 85%;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"